body {
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 1rem 0;
}

.container {
  max-width: 800px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.breed-select-container {
  margin-bottom: 20px;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
  display: block;
}

.breed-select {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.loader, .error {
  text-align: center;
  padding: 10px;
  display: none;
}

.cat-info {
  text-align: center;
  margin-top: 20px;
  display: none;
}

.cat-info h2 {
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-top: 10px;
}

